import React from 'react';
import World from './panel2/World';
import Mexico from './panel2/Mexico';
import Morelos from './panel2/Morelos';

//Apis
// https://documenter.getpostman.com/view/10808728/SzS8rjbc?version=latest#7934d316-f751-4914-9909-39f1901caeb8
// https://api.covid19api.com/dayone/country/south-africa
// https://api.covid19api.com/summary
// https://api.covid19api.com/country/mexico/status/confirmed?from=2020-07-01T00:00:00Z&to=2020-07-29T00:00:00Z
// https://api.covid19api.com/world/total
// https://react-charts.js.org/examples/dynamic-parent

class Panel2 extends React.Component{
    
    render = () =>{
        const {covid_state} = this.props;
        return(
            <section className="panel2__content overflow-auto">
                {
                    (covid_state === 2) ? <Mexico />
                    : (covid_state === 3) ? <Morelos />
                    : <World />
                }
            </section>
        );
    }
}

export default Panel2;